













import Vue from "vue";
import api from "../service/api";
import { Company } from "../service/interfaces";
export default Vue.extend({
  name: "company",
  data() {
    return {
      model: <Company>{},
    };
  },
  methods: {
    async submit() {
      try {
        await api.post("/company", this.model);
      } catch (err) {
        console.error(err);
      }
    },
  },
  computed: {},
});
